import {
	Box,
	Button,
	Grid,
	Pagination,
	Typography,
	useMediaQuery,
} from '@mui/material'
import Backdrop from '@mui/material/Backdrop'
import CircularProgress from '@mui/material/CircularProgress'
import { useTheme } from '@mui/styles'
import React, { useEffect } from 'react'
import { useNavigate, useParams } from 'react-router'

import { useDispatch } from 'react-redux'
import { GetCategoryList } from '../../reducers/main/main-api'
import { Link } from 'react-router-dom'
import { Container } from '@mui/system'

const Products = ({ home }) => {
	const theme = useTheme()
	const lg = useMediaQuery(theme.breakpoints.down('992'))
	const navigate = useNavigate()
	const params = useParams()
	const dispatch = useDispatch()
	useEffect(() => {
		try {
			dispatch(GetCategoryList(params.title))
		} catch (e) {}
	}, [])
	return (
		<Container maxWidth='xl'>
			{home.length === 0 ? (
				<Box
					sx={{
						width: '100%',
						display: 'flex',
						justifyContent: 'center',
					}}
				>
					<CircularProgress color='inherit' />
				</Box>
			) : (
				<Box width={{ xs: '100%' }} ml={{ xs: 0, lg: 0 }}>
					<Grid container spacing={{ xs: 2, lg: 4 }} mb={5}>
						{home.map((item, index) => {
							return (
								<Grid item xs={6} lg={3}>
									<Link
										key={index}
										to={`/catalog/details/${item.title}`}
										style={{ display: 'block' }}
									>
										<img
											src={item.image}
											style={{
												width: '100%',
												maxWidth: '270px',
												display: 'table',
												margin: '0 auto',
												height: lg ? 135 : '270px',
												objectFit: 'cover',
											}}
											alt=''
										/>
										<Typography variant='subtitle2' mt={1} textAlign='center'>
											{item.title}
										</Typography>
									</Link>
								</Grid>
							)
						})}
					</Grid>
				</Box>
			)}
			{home && (
				<Button
					size='medium'
					onClick={() => navigate('/catalog')}
					sx={{ display: 'table', m: '44px auto 0' }}
					color='primary'
					variant='contained'
				>
					  
				</Button>
			)}
		</Container>
	)
}

export default Products
